.register__submit-button{
    padding: 0;
    margin-top: 180px;
    background-color: #fff;
    color: black;
    border: none;
    border-radius: 2px;
    width: 100%;
    height: 50px;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;
    cursor: pointer;
  }
  
  .register__submit-button:hover{
    opacity: 0.8;
    transition: opacity 2s ease;
  }